    description:
      "Constantinopol, 1453  de-o parte i de alta a zidurilor oraului, doi copii, Anna i Omeir, sunt sortii s se ntlneasc, pentru a salva o carte i, prin gestul lor, pe ei nii. Idaho, n prezent  la etajul bibliotecii publice, octogenarul Zeno mpreun cu cinci copii monteaz o bizar pies de teatru, n vreme ce, la parter, adolescentul idealist Seymour se pregtete s detoneze o bomb, creznd c lupt pentru o cauz nobil. n spaiu, peste cteva decenii  Konstance, pe o nav interstelar avnd ca destinaie o exoplanet, supravieuiete doar cluzit de o poveste pe care i-o spunea tatl ei sear de sear. Destinele tuturor personajelor sunt legate de misteriosul manuscris din vechime, care celebreaz puterea scrisului i a imaginaiei.",
    released: `2021-09-28`,
